import React from 'react';
import {NavLink} from 'react-router-dom';
import './Navbar.css';

class Navbar extends React.Component {

//render decides what is displayed on the page
render() {
    return (
        <nav className="navbar">
            {/* these links change the current route */}
            <NavLink to="/" className="navbar__link" >Who Am I</NavLink>
            <NavLink to="/projects" className="navbar__link" >What I do</NavLink>
            <NavLink to="/contact"className="navbar__link">Say hello</NavLink>

        </nav>
    )
}




}

export default Navbar;