import React, { Component } from "react";
import Slider from "react-slick";
import './ContentHome.css';
import '../../node_modules/slick-carousel/slick/slick.css'
import '../../node_modules/slick-carousel/slick/slick-theme.css'


export default class CenterModeCarousel extends Component {
    render() {
      const settings = {
        className: "center",
        centerMode: true,
        infinite: true,
        centerPadding: "2em",
        slidesToShow: 5,
        dots: true,
        accessibility: true,
        swipeToSlide: true,
        autoplay: true,
        speed: 2000
      };
      const images = [
        'https://raw.githubusercontent.com/buzzfair/cultcodeSmr2020Cohort/f9586ab5f001f77823c20839bdc6248e6373ea83/images/adobe-xd.svg',
        'https://raw.githubusercontent.com/buzzfair/cultcodeSmr2020Cohort/f9586ab5f001f77823c20839bdc6248e6373ea83/images/figma-1.svg',
        'https://raw.githubusercontent.com/buzzfair/cultcodeSmr2020Cohort/f9586ab5f001f77823c20839bdc6248e6373ea83/images/javascript-4.svg',
        'https://raw.githubusercontent.com/buzzfair/cultcodeSmr2020Cohort/f9586ab5f001f77823c20839bdc6248e6373ea83/images/sass-1.svg',
        'https://raw.githubusercontent.com/buzzfair/cultcodeSmr2020Cohort/f9586ab5f001f77823c20839bdc6248e6373ea83/images/ui-ux-grey20181026.svg',
        'https://raw.githubusercontent.com/buzzfair/cultcodeSmr2020Cohort/f9586ab5f001f77823c20839bdc6248e6373ea83/images/bootstrap-icon.svg',
        'https://raw.githubusercontent.com/buzzfair/cultcodeSmr2020Cohort/f9586ab5f001f77823c20839bdc6248e6373ea83/images/css-5.svg',
        'https://raw.githubusercontent.com/buzzfair/cultcodeSmr2020Cohort/f9586ab5f001f77823c20839bdc6248e6373ea83/images/dribbble-icon-1.svg',
        'https://raw.githubusercontent.com/buzzfair/cultcodeSmr2020Cohort/f9586ab5f001f77823c20839bdc6248e6373ea83/images/react-2.svg',
        'https://raw.githubusercontent.com/buzzfair/cultcodeSmr2020Cohort/f9586ab5f001f77823c20839bdc6248e6373ea83/images/html5.svg',
        'https://raw.githubusercontent.com/buzzfair/cultcodeSmr2020Cohort/f9586ab5f001f77823c20839bdc6248e6373ea83/images/invision.svg',
      ];
      return (
        <div className="icon-carousel">
          <Slider {...settings}>
            {images.map((image, key) => <div className="carousel-slides" key={key}>
                <img className="carousel-images" alt="Tools and Technology I Use" src={image} /></div>)}
          </Slider>
        </div>
      );
    }
  }
  
  

  